
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data(){
return {
refund_id: "",
list: "",
nvabarData: {
showCapsule: 1, //是否显示左上角图标
title: '鞋品荟', //导航栏 中间的标题
},
}
},
created(){
this.refund_id = this.$route.query.refund_id;
this.showsteps();
},
methods: {
async showsteps(){
let data = {
plat: 3,
account: this.account,
token: this.token,
refund_id: this.refund_id
}
const [err, res] = await api.refundconsult(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
for (var i = 0; i < res.data.length; i++) {
res.data[i].log_desc = res.data[i].log_desc.split("\\n");
res.data[i].log_time = this.getLocalTime(res.data[i].log_time);
}
this.list = res.data;
}
},
getLocalTime(nS){
var time = new Date(nS * 1000);
var date = (time.getFullYear()) + "-" +
(time.getMonth() + 1) + "-" +
(time.getDate()) + " " +
(time.getHours()) + ":" +
(time.getMinutes()) + ":" +
(time.getSeconds());
return date;
},
},
computed: {
...mapGetters(["user", "account", "token"])
}
}



